.popup {
  background-color: $white;
  max-width: 874px;
  width: 100%;
  border-radius: 20px;
  padding: 48px 48px 40px;
  z-index: 210;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  &__close {
    position: absolute;
    right: 16px;
    top: 16px;
  }
  &__bg {
    position: fixed;
    top: 0;
    left: 0;
    display: none;
    width: 100%;
    height: 100%;
    overflow-y: auto;
    background-color: rgba($text, 0.8);
    z-index: 200;
    padding: 80px 0;
  }
  &__header {
    display: flex;
    justify-content: space-between;
    padding-bottom: 16px;
    border-bottom: 1px solid $gray-200;
    flex-direction: column-reverse;
    gap: 11px;
    @include screen(laptop) {
      flex-direction: row;
      padding-bottom: 24px;
      align-items: center;
    }
    &__left {
      max-width: 300px;
    }
    &__icon {
      background-color: $gray-200;
      border-radius: 50%;
      img {
        width: 50px;
        height: 50px;
        min-width: 50px;
        min-height: 50px;
      }
    }
    &__right {
      display: flex;
      align-items: center;
      gap: 16px;
      span {
        text-transform: uppercase;
        color: #30429b;
        font-weight: 700;
      }
    }
  }
  &__title {
    font-size: 26px;
    line-height: 100%;
    font-weight: 500;
    margin-bottom: 16px;
    @include screen(laptop) {
      font-size: 32px;
    }
  }
  &__description {
    font-size: 16px;
    line-height: 100%;
    font-weight: 500;
    color: $blue-400;
    @include screen(laptop) {
      font-size: 20px;
    }
  }
  &__subtitle {
    font-weight: 500;
    color: $blue-400;
    font-size: 15px;
    line-height: 100%;
    @include screen(laptop) {
      font-size: 17px;
    }
  }
  &__container {
    border-bottom: 1px solid $gray-200;
    padding: 16px 0;
    ul {
      max-width: 600px;
      margin-top: 12px;
      list-style: disc;
      padding-left: 20px;
      li {
        font-weight: 500;
        font-size: 16px;
        line-height: 100%;
        margin-top: 10px;
        @include screen(laptop) {
          font-size: 18px;
        }
      }
    }
    @include screen(laptop) {
      padding: 24px 0 20px;
    }
  }
  &__conditions {
    margin-top: 14px;
    display: grid;
    column-gap: 20px;
    grid-template-columns: 1fr;
    row-gap: 12px;
    @include screen(laptop) {
      grid-template-columns: repeat(2, minmax(0, 1fr));
    }
  }
  &__condition {
    padding: 10px 22px;
    display: flex;
    height: 88px;
    align-items: center;
    gap: 20px;
    border: 1px solid #eaebf0;
    background-color: #f8f8fb;
    filter: drop-shadow(0px 3px 7px rgba(105, 120, 195, 0.2));
    @include screen(tablet) {
      background-color: transparent;
      border: none;
      justify-content: center;
    }
    @include screen(laptop) {
      justify-content: flex-start;
    }
    &__icon {
      z-index: 2;
      position: relative;
      img {
        min-width: 44px;
        max-height: 44px;
      }
    }
    &__bg {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      display: none;
      height: 100%;
      img {
        max-height: 100%;
        object-fit: cover;
        @include screen(laptop) {
          width: 100%;
        }
      }
      @include screen(tablet) {
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }
    span {
      font-size: 13px;
      font-weight: 500;
      line-height: 124%;
      z-index: 2;
      position: relative;
      @include screen(laptop) {
        font-size: 17px;
      }
    }
  }
  &__tip {
    font-size: 22px;
    margin-top: 16px;
    font-weight: 500;
    line-height: 100%;
    color: $blue-400;
    @include screen(laptop) {
      margin-top: 28px;
    }
    a {
      font-weight: 700;
    }
  }
  &__form {
    position: relative;
    z-index: 2;

    display: grid;
    grid-template-columns: repeat(1, minmax(0, 1fr));
    gap: 16px;
    margin-top: 16px;
    @include screen(laptop) {
      margin-top: 40px;
      grid-template-columns: repeat(3, minmax(0, 1fr));
      margin-top: 32px;
    }
  }
  &__labels {
    display: flex;
    grid-column: span 1;
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
    width: 100%;
    @include screen(laptop) {
      grid-column: span 2;
    }
  }
  &__label {
    position: relative;

    display: block;
    width: 100%;
    &:before,
    &:after {
      content: "";

      position: absolute;
    }
    &:before {
      top: 0;
      left: 0;

      border: 10px solid transparent;
      border-top: 0px solid $white;
      border-left: 20px solid $white;
    }
    &:after {
      right: 0;
      bottom: 0;

      border: 10px solid transparent;
      border-bottom: 0px solid $white;
      border-right: 20px solid $white;
    }
  }
  &__button {
    button {
      position: relative;

      padding: 12px 54px;
      width: 100%;

      background-color: $blue-400;

      font-weight: 500;
      font-size: 18px;
      letter-spacing: -0.36px;
      color: $white;
      &:before,
      &:after {
        content: "";

        position: absolute;
      }
      &:before {
        top: 0;
        left: 0;

        border: 10px solid transparent;
        border-top: 0px solid $white;
        border-left: 20px solid $white;
      }
      &:after {
        right: 0;
        bottom: 0;

        border: 10px solid transparent;
        border-bottom: 0px solid $white;
        border-right: 20px solid $white;
      }
      @include screen(laptop) {
        transition: background-color $transition-default;
        &:hover {
          background-color: $blue-500;
        }
      }
    }
    @include screen(laptop) {
      grid-column: span 1;
    }
  }
  &__upload {
    position: relative;

    display: block;
    padding: 6px 12px;
    width: 100%;

    background-color: $blue-400;

    font-weight: 500;
    font-size: 18px;
    letter-spacing: -0.36px;
    text-align: center;
    color: $white;
    cursor: pointer;
    input {
      position: absolute;

      width: 0;
      height: 0;
      opacity: 0;
    }
    &:before,
    &:after {
      content: "";

      position: absolute;
    }
    &:before {
      top: 0;
      left: 0;

      border: 10px solid transparent;
      border-top: 0px solid #dbdff2;
      border-left: 10px solid #dbdff2;
      @include screen(laptop) {
        border: 10px solid transparent;
        border-top: 0px solid #dbdff2;
        border-left: 20px solid #dbdff2;
      }
    }
    &:after {
      right: 0;
      bottom: 0;

      border: 10px solid transparent;
      border-bottom: 0px solid #dbdff2;
      border-right: 10px solid #dbdff2;
      @include screen(laptop) {
        border: 10px solid transparent;
        border-bottom: 0px solid #dbdff2;
        border-right: 20px solid #dbdff2;
      }
    }
    @include screen(laptop) {
      transition: background-color $transition-default;
      &:hover {
        background-color: $blue-500;
      }
    }
  }
  &__block {
    position: relative;

    padding: 1px;
    overflow: hidden;

    background-color: #bbc2e3;
    &__inner {
      padding: 28px 32px;

      background-color: #dbdff2;
      &:before {
        content: "";

        position: absolute;
        top: 3px;
        left: -3px;

        width: 30px;
        height: 1px;

        background-color: #bbc2e3;
        transform: rotate(-26deg);
      }
      &:after {
        content: "";

        position: absolute;
        right: -3px;
        bottom: 4px;

        width: 30px;
        height: 1px;

        background-color: #bbc2e3;
        transform: rotate(-27deg);
      }
      @include screen(laptop) {
        padding: 20px 30px;
      }
    }
    h3 {
      margin-bottom: 18px;

      font-size: 17px;
      line-height: 120%;
      letter-spacing: -0.4px;
      text-align: center;
      color: $blue-400;
      @include screen(laptop) {
        font-size: 20px;
      }
    }
    p {
      margin-bottom: 18px;

      font-size: 15px;
      line-height: 120%;
      letter-spacing: -0.4px;
      text-align: center;
      color: $blue-400;
    }
    &:before,
    &:after {
      content: "";

      position: absolute;
    }
    &:before {
      top: 0;
      left: 0;

      border: 10px solid transparent;
      border-top: 0px solid $white;
      border-left: 20px solid $white;
    }
    &:after {
      right: 0;
      bottom: 0;

      border: 10px solid transparent;
      border-bottom: 0px solid $white;
      border-right: 20px solid $white;
    }
    @include screen(laptop) {
      grid-column: span 1;
    }
  }
  &__input {
    padding: 10px 24px;
    width: 100%;
    height: 44px;
    background-color: $gray-400;
  }
}