.hero {
  padding: 209px 0 44px;

  background: url("../../assets/images/hero-bg.png") 50% 50% no-repeat;
  background-size: cover;
  &__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 16px;
    @include screen(laptop) {
      flex-direction: row;
      align-items: flex-end;
    }
  }
  &__image {
    margin-bottom: -40px;
    width: 100%;
    img {
      width: 100%;
    }
    @include screen(laptop) {
      max-width: 609px;
    }
  }
  &__title {
    margin-bottom: 8px;

    font-weight: 700;
    font-size: 28px;
    line-height: 100%;
    letter-spacing: -1.12px;
    color: $white;
    @include screen(laptop) {
      margin-bottom: 38px;

      font-size: 56px;
    }
  }
  &__description {
    font-size: 19px;
    line-height: 140%;
    letter-spacing: -0.38px;
    color: $gray-200;
    p {
      margin-top: 16px;
      &:first-child {
        margin-top: 0;
      }
    }
    @include screen(laptop) {
      padding-left: 20px;
      max-width: 611px;

      border-left: 1px solid $blue-600;
    }
  }
}