.directions {
  padding: 36px 0;
  &__image {
    filter: drop-shadow(0px 3px 7px rgba(105, 120, 195, 0.2));

    display: none;
    width: 100%;
    @include screen(tablet) {
      display: block;
    }
    &-mob {
      display: block;
      @include screen(tablet) {
        display: none;
      }
    }
  }
  @include screen(laptop) {
    padding: 56px 0;
  }
}