.advantages {
  padding: 32px 0 72px;
  &__items {
    display: grid;
    grid-template-columns: repeat(1, minmax(0, 1fr));
    gap: 20px;
    @include screen(tablet) {
      grid-template-columns: repeat(2, minmax(0, 1fr));
    }
    @include screen(laptop) {
      grid-template-columns: repeat(4, minmax(0, 1fr));
    }
  }
  .advantage {
    position: relative;

    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    gap: 12px;
    padding: 21px 40px 13px 18px;
    overflow: hidden;

    filter: drop-shadow(0px 3px 7px rgba(105, 120, 195, 0.2));
    &__bg {
      position: absolute;
      top: 0;
      left: 0;

      width: 100%;
      img {
        width: 100%;
        object-fit: cover;
        object-position: center;
      }
    }
    &__title {
      position: relative;
      z-index: 2;

      display: flex;
      align-items: flex-end;
      min-height: 87px;

      font-weight: 500;
      font-size: 18px;
      line-height: 110%;
      letter-spacing: -0.18px;
      color: $blue-400;
    }
    &__icon {
      position: absolute;
      right: 20px;
      z-index: 2;

      display: flex;
      align-items: center;
      img {
        transform: translateY(35%);
      }
    }
    @include screen(laptop) {
      border: none;
    }
  }
  @include screen(laptop) {
    padding: 96px 0 112px;
  }
}