.footer {
  background-color: $text;
  &__header {
    display: grid;
    grid-template-columns: repeat(1, minmax(0, 1fr));
    align-items: center;
    row-gap: 32px;
    column-gap: 22px;
    padding: 32px 0;

    border-bottom: 1px solid rgba($white, 0.15);
    @include screen(tablet) {
      grid-template-columns: repeat(2, minmax(0, 1fr));
    }
    @include screen(laptop) {
      grid-template-columns: repeat(3, minmax(0, 1fr));
      gap: 22px;
      padding: 24px 0 20px;
    }
  }
  &__logo {
    margin: 0 auto;
    max-width: 243px;
    @include screen(tablet) {
      grid-row: span 2;
      max-width: 100%;
    }
    @include screen(laptop) {
      grid-row: span 1;
    }
  }
  &__links {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    row-gap: 16px;
    column-gap: 10px;
    .footer__link {
      width: 100%;
      @include screen(laptop) {
        width: auto;
      }
    }
    @include screen(laptop) {
      flex-wrap: nowrap;
      row-gap: 0;
    }
  }
  &__contact {
    position: relative;

    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 12px;
    .footer__link {
      border-bottom: 1px solid transparent;

      transition: border-color $transition-default;
      &:hover {
        border-color: $white;
      }
    }
    &:last-child {
      .footer__links {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: 22px;
      }
      .footer__icon {
        margin-left: 0;
        svg {
          max-width: 62px;
          max-height: 50px;
          @include screen(tablet) {
            margin-left: 10px;
            max-width: 100%;
            max-height: 100%;
          }
        }
        @include screen(tablet) {
          position: absolute;
          top: -16px;
          right: 0;
          filter: drop-shadow(0px 8.859px 19.933px rgba(24, 39, 75, 0.24));
        }
      }
      @include screen(tablet) {
        grid-column-start: 2;
      }
      @include screen(laptop) {
        grid-column-start: auto;
      }
    }
  }
  &__subtitle {
    font-weight: 700;
    font-size: 15px;
    line-height: 80%;
    text-transform: uppercase;
    color: $blue-400;
  }
  &__body {
    display: grid;
    grid-template-columns: repeat(1, minmax(0, 1fr));
    align-items: center;
    gap: 22px;
    padding: 20px 0;

    border-bottom: 1px solid rgba($white, 0.15);
    @include screen(tablet) {
      grid-template-columns: repeat(2, minmax(0, 1fr));
    }
    @include screen(laptop) {
      grid-template-columns: repeat(3, minmax(0, 1fr));
    }
  }
  &__block {
    position: relative;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    padding: 20px 18px;
    min-height: 154px;

    background-color: rgba($blue-200, 0.2);
    &:before,
    &:after {
      content: "";

      position: absolute;
    }
    &:before {
      bottom: 0;
      left: 0;

      border: 10px solid transparent;
      border-bottom: 5px solid $text;
      border-left: 20px solid $text;
      @include screen(laptop) {
        border: 20px solid transparent;
        border-bottom: 10px solid $text;
        border-left: 40px solid $text;
      }
    }
    &:after {
      top: 0;
      right: 0;

      border: 10px solid transparent;
      border-top: 5px solid $text;
      border-right: 20px solid $text;
      @include screen(laptop) {
        border: 20px solid transparent;
        border-top: 10px solid $text;
        border-right: 40px solid $text;
      }
    }
  }
  &__title {
    display: flex;
    align-items: center;
    gap: 10px;
    span {
      font-weight: 700;
      font-size: 24px;
      line-height: 100%;
      text-transform: uppercase;
    }
    &-orange {
      span {
        color: $orange-400;
      }
    }
    &-green {
      span {
        color: $green-400;
      }
    }
    &-lightgreen {
      span {
        color: $lightgreen-400;
      }
    }
  }
  &__link {
    font-weight: 700;
    font-size: 28px;
    line-height: 80%;
    letter-spacing: -0.56px;
    color: $white;
    &-small {
      font-weight: 500;
      font-size: 16px;
      line-height: 20px;
      letter-spacing: -0.32px;
      color: rgba($white, 0.3);
      text-decoration-line: underline;
    }
  }
  &__phone {
    display: flex;
    align-items: center;
    align-self: flex-end;
    gap: 22px;
    a {
      font-weight: 500;
      font-size: 22px;
      line-height: 80%;
      letter-spacing: -0.44px;
      color: $white;
    }
    .footer__link {
      border-bottom: 1px solid transparent;

      transition: border-color $transition-default;
      &:hover {
        border-color: $white;
      }
    }
  }
  &__external {
    display: flex;
    align-items: center;
    gap: 6px;
    span {
      color: $blue-500;
    }
  }
  &__copy {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding: 14px 0;
    .footer__links {
      flex-direction: column;
      align-items: flex-start;
      gap: 15px;
      @include screen(tablet) {
        flex-direction: row;
        align-items: center;
        gap: 24px;
      }
    }
    & > .footer__link {
      &:last-child {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        gap: 8px;
        span {
          line-height: 20px;
          text-align: right;
        }
      }
    }
    @include screen(tablet) {
      align-items: center;
    }
  }
}