.header {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 100;

  padding: 20px 0;
  width: 100%;
  &__logo {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 12px;
    min-width: 180px;
    max-width: 180px;
    img {
      &:last-child {
        display: none;
        @include screen(laptop) {
          display: block;
        }
      }
    }
    @include screen(tablet) {
      margin-right: 88px;
      min-width: auto;
      max-width: 100%;
    }
  }
  &__wrapper {
    position: relative;

    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__contacts {
    display: flex;
    align-items: center;
    gap: 8px;
    .header__phone {
      &-mob {
        display: inline-block;
        @include screen(laptop) {
          display: none;
        }
      }
    }
    svg {
      width: 35px;
      height: 35px;
      @include screen(laptop) {
        width: 44px;
        height: 44px;
      }
    }
  }
  &__phone {
    display: none;
    padding: 8px 23px;

    border: 2px solid $blue-400;
    border-radius: 33px;

    font-weight: 700;
    color: $white;
    &-mob {
      display: block;
      padding: 0;

      border: none;
      @include screen(laptop) {
        display: none;
      }
    }
    @include screen(laptop) {
      display: block;

      transition: background-color $transition-default;
      &:hover {
        background-color: $blue-400;
      }
    }
  }
  &__left {
    display: flex;
    flex: 1;
    justify-content: space-between;
    align-items: center;
  }
  &__nav {
    position: absolute;
    bottom: -100px;

    display: flex;
    align-items: center;
    gap: 24px;
    padding: 14px 0;
    width: 100%;

    border-top: 1px solid $blue-500;
    border-bottom: 1px solid $blue-500;
    a {
      display: flex;
      flex-direction: column;
      align-items: center;
      align-items: flex-start;
      gap: 4px;
      svg {
        min-width: 20px;
        min-height: 20px;
      }
      span {
        border-bottom: 1px solid transparent;

        font-weight: 600;
        color: $white;

        transition: border-color $transition-default;
      }
      &:hover {
        span {
          border-color: $white;
        }
      }
      @include screen(laptop) {
        flex-direction: row;
        gap: 8px;
      }
    }
    @include screen(tablet) {
      position: relative;
      bottom: auto;

      gap: 60px;

      border: none;
    }
    @include screen(laptop) {
      justify-content: center;
    }
  }
}