.heading {
  position: relative;

  display: flex;
  align-items: center;
  gap: 20px;
  margin-bottom: 16px;
  .swiper-navigation {
    display: flex;
    align-items: center;
    gap: 6px;
  }
  .swiper-button-prev,
  .swiper-button-next {
    position: static;

    margin: 0;
    max-width: 40px;
    max-height: 40px;

    transition: opacity $transition-default;
    &:before,
    &:after {
      display: none;
    }
  }
  @include screen(laptop) {
    margin-bottom: 32px;
  }
}
.title {
  font-weight: 500;
  font-size: 26px;
  letter-spacing: -0.72px;
  &-white {
    color: $white;
  }
  @include screen(laptop) {
    font-size: 36px;
  }
}