.callback {
  padding: 72px 0 36px;
  .heading {
    margin-bottom: 0;
  }
  &__image {
    position: absolute;
    top: 16px;
    right: 16px;

    max-width: 172px;
    img {
      width: 100%;
    }
    @include screen(laptop) {
      position: relative;
      top: 0;
      right: 0;

      max-width: 410px;
    }
  }
  &__content {
    flex: 1;
    .title {
      margin-bottom: 10px;
      max-width: 160px;

      line-height: 30px;
      @include screen(laptop) {
        max-width: 100%;

        line-height: 1;
      }
    }
  }
  &__form {
    position: relative;
    z-index: 2;

    display: grid;
    grid-template-columns: repeat(1, minmax(0, 1fr));
    gap: 16px;
    margin-top: 40px;
    @include screen(laptop) {
      grid-template-columns: repeat(3, minmax(0, 1fr));
      margin-top: 32px;
      max-width: 750px;
    }
  }
  &__block {
    position: relative;

    padding: 1px;
    overflow: hidden;

    background-color: #bbc2e3;
    &__inner {
      padding: 28px 32px;

      background-color: #dbdff2;
      &:before {
        content: "";

        position: absolute;
        top: 3px;
        left: -3px;

        width: 30px;
        height: 1px;

        background-color: #bbc2e3;
        transform: rotate(-26deg);
      }
      &:after {
        content: "";

        position: absolute;
        right: -3px;
        bottom: 4px;

        width: 30px;
        height: 1px;

        background-color: #bbc2e3;
        transform: rotate(-27deg);
      }
      @include screen(laptop) {
        padding: 32px 40px;
      }
    }
    h3 {
      margin-bottom: 18px;

      font-size: 17px;
      line-height: 120%;
      letter-spacing: -0.4px;
      text-align: center;
      color: $blue-400;
      @include screen(laptop) {
        font-size: 20px;
      }
    }
    p {
      margin-bottom: 18px;

      font-size: 15px;
      line-height: 120%;
      letter-spacing: -0.4px;
      text-align: center;
      color: $blue-400;
    }
    &:before,
    &:after {
      content: "";

      position: absolute;
    }
    &:before {
      top: 0;
      left: 0;

      border: 10px solid transparent;
      border-top: 0px solid $gray-100;
      border-left: 20px solid $gray-100;
    }
    &:after {
      right: 0;
      bottom: 0;

      border: 10px solid transparent;
      border-bottom: 0px solid $gray-100;
      border-right: 20px solid $gray-100;
    }
    @include screen(laptop) {
      grid-column: span 1;
    }
  }
  &__wrapper {
    position: relative;

    display: flex;
    padding: 28px 20px 20px;

    background-color: $gray-100;
    &:before,
    &:after {
      content: "";

      position: absolute;
    }
    &:before {
      top: 0;
      left: 0;

      border: 20px solid transparent;
      border-top: 10px solid $white;
      border-left: 40px solid $white;
      @include screen(laptop) {
        border: 40px solid transparent;
        border-top: 20px solid $white;
        border-left: 80px solid $white;
      }
    }
    &:after {
      right: 0;
      bottom: 0;

      border: 20px solid transparent;
      border-bottom: 10px solid $white;
      border-right: 40px solid $white;
      @include screen(laptop) {
        border: 40px solid transparent;
        border-bottom: 20px solid $white;
        border-right: 80px solid $white;
      }
    }
    @include screen(laptop) {
      padding: 64px 87px 0 56px;
    }
  }
  &__text {
    max-width: 82px;

    font-weight: 500;
    font-size: 18px;
    line-height: 20px;
    letter-spacing: -0.44px;
    color: $blue-400;
    @include screen(laptop) {
      max-width: 100%;

      font-size: 22px;
    }
  }
  &__labels {
    display: flex;
    grid-column: span 1;
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
    width: 100%;
    @include screen(laptop) {
      grid-column: span 2;
    }
  }
  &__label {
    position: relative;

    display: block;
    width: 100%;
    &:before,
    &:after {
      content: "";

      position: absolute;
    }
    &:before {
      top: 0;
      left: 0;

      border: 10px solid transparent;
      border-top: 0px solid $gray-100;
      border-left: 20px solid $gray-100;
    }
    &:after {
      right: 0;
      bottom: 0;

      border: 10px solid transparent;
      border-bottom: 0px solid $gray-100;
      border-right: 20px solid $gray-100;
    }
  }
  &__button {
    button {
      position: relative;

      padding: 12px 54px;
      width: 100%;

      background-color: $blue-400;

      font-weight: 500;
      font-size: 18px;
      letter-spacing: -0.36px;
      color: $white;
      &:before,
      &:after {
        content: "";

        position: absolute;
      }
      &:before {
        top: 0;
        left: 0;

        border: 10px solid transparent;
        border-top: 0px solid $gray-100;
        border-left: 20px solid $gray-100;
      }
      &:after {
        right: 0;
        bottom: 0;

        border: 10px solid transparent;
        border-bottom: 0px solid $gray-100;
        border-right: 20px solid $gray-100;
      }
      @include screen(laptop) {
        transition: background-color $transition-default;
        &:hover {
          background-color: $blue-500;
        }
      }
    }
    @include screen(laptop) {
      grid-column: span 1;
    }
  }
  &__upload {
    position: relative;

    display: block;
    padding: 6px 12px;
    width: 100%;

    background-color: $blue-400;

    font-weight: 500;
    font-size: 18px;
    letter-spacing: -0.36px;
    text-align: center;
    color: $white;
    cursor: pointer;
    input {
      position: absolute;

      width: 0;
      height: 0;
      opacity: 0;
    }
    &:before,
    &:after {
      content: "";

      position: absolute;
    }
    &:before {
      top: 0;
      left: 0;

      border: 10px solid transparent;
      border-top: 0px solid #dbdff2;
      border-left: 10px solid #dbdff2;
      @include screen(laptop) {
        border: 10px solid transparent;
        border-top: 0px solid #dbdff2;
        border-left: 20px solid #dbdff2;
      }
    }
    &:after {
      right: 0;
      bottom: 0;

      border: 10px solid transparent;
      border-bottom: 0px solid #dbdff2;
      border-right: 10px solid #dbdff2;
      @include screen(laptop) {
        border: 10px solid transparent;
        border-bottom: 0px solid #dbdff2;
        border-right: 20px solid #dbdff2;
      }
    }
    @include screen(laptop) {
      transition: background-color $transition-default;
      &:hover {
        background-color: $blue-500;
      }
    }
  }
  &__input {
    padding: 10px 24px;
    width: 100%;
    height: 44px;
  }
  @include screen(laptop) {
    padding: 112px 0 56px;
  }
}