.gallery {
  padding: 36px 0 72px;
  overflow-x: hidden;
  &__image {
    display: none;
    width: 100%;
    &-mob {
      display: block;
      @include screen(tablet) {
        display: none;
      }
    }
    @include screen(tablet) {
      display: block;
    }
  }
  .swiper {
    overflow: visible;
  }
  &__slide {
    position: relative;

    overflow: hidden;
    &:before,
    &:after {
      content: "";

      position: absolute;
    }
    &:before {
      top: 0;
      left: 0;

      border: 20px solid transparent;
      border-top: 10px solid $white;
      border-left: 40px solid $white;
      @include screen(laptop) {
        border: 40px solid transparent;
        border-top: 20px solid $white;
        border-left: 80px solid $white;
      }
    }
    &:after {
      right: 0;
      bottom: 0;

      border: 20px solid transparent;
      border-bottom: 10px solid $white;
      border-right: 40px solid $white;
      @include screen(laptop) {
        border: 40px solid transparent;
        border-bottom: 20px solid $white;
        border-right: 80px solid $white;
      }
    }
    img {
      width: 100%;
      height: 216px;
      max-height: 216px;
      object-fit: cover;
      @include screen(tablet) {
        height: 216px;
        max-height: 216px;
      }
      @include screen(laptop) {
        height: 350px;
        max-height: 350px;
      }
    }
  }
  @include screen(laptop) {
    padding: 56px 0 112px;
  }
}