.vacancies {
  position: relative;

  margin: 0 16px;
  padding: 72px 0;
  overflow-x: hidden;

  background-color: $blue-200;
  &:before,
  &:after {
    content: "";

    position: absolute;
  }
  &:before {
    top: 0;
    left: 0;

    border: 20px solid transparent;
    border-top: 10px solid $white;
    border-left: 40px solid $white;
    @include screen(laptop) {
      border: 40px solid transparent;
      border-top: 20px solid $white;
      border-left: 80px solid $white;
    }
  }
  &:after {
    right: 0;
    bottom: 0;

    border: 20px solid transparent;
    border-bottom: 10px solid $white;
    border-right: 40px solid $white;
    @include screen(laptop) {
      border: 40px solid transparent;
      border-bottom: 20px solid $white;
      border-right: 80px solid $white;
    }
  }
  &__wrapper {
    .heading {
      margin-bottom: 0;
    }
  }
  &__buttons {
    display: inline-flex;
    align-items: stretch;
    gap: 3px;
    margin-top: 16px;
    padding: 4px;

    border-radius: 100px;
    background-color: rgba($text, 0.4);
    @include screen(laptop) {
      margin-top: 24px;
      padding: 3px;
    }
  }
  &__title {
    display: flex;
    align-items: center;
    gap: 17px;
    padding-right: 48px;
    span {
      font-weight: 700;
      font-size: 20px;
      line-height: 100%;
      text-transform: uppercase;
      color: $white;
      @include screen(tablet) {
        max-width: 80%;

        word-break: normal;
      }
      @include screen(laptop) {
        max-width: 100%;

        font-size: 30px;
      }
    }
    img {
      width: 44px;
      min-width: 44px;
      height: 44px;
      min-height: 44px;
      @include screen(laptop) {
        width: 48px;
        min-width: 48px;
        height: 48px;
        min-height: 48px;
      }
    }
    @include screen(tablet) {
      padding-right: 0;
    }
  }
  &__element {
    margin-top: 40px;
    @include screen(laptop) {
      margin-top: 56px;
    }
  }
  &__header {
    position: relative;

    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 20px;
    &__left {
      display: flex;
      flex-direction: column;
      gap: 16px;
      width: 100%;
      @include screen(laptop) {
        flex-direction: row;
        align-items: center;
        gap: 40px;
      }
    }
    &__toggle {
      position: absolute;
      top: 8px;
      right: 0;

      transition: transform $transition-default;
      svg {
        width: 32px;
        min-width: 32px;
        height: 32px;
        min-height: 32px;
        @include screen(laptop) {
          width: 48px;
          min-width: 48px;
          height: 48px;
          min-height: 48px;
        }
      }
      @include screen(laptop) {
        position: static;
      }
    }
    &.active {
      .vacancies__header__toggle {
        transform: rotate(180deg);
      }
    }
    @include screen(laptop) {
      border-bottom: 1px solid rgba($white, 0.2);
    }
  }
  &__links {
    display: flex;
    flex-wrap: wrap;
    align-content: center;
    row-gap: 12px;
    column-gap: 20px;
    padding-top: 20px;

    border-top: 1px solid rgba($white, 0.2);
    @include screen(laptop) {
      flex-wrap: nowrap;
      gap: 32px;
      padding-top: 0;

      border-top: none;
    }
  }
  &__link {
    display: flex;
    align-items: center;
    gap: 8px;

    font-weight: 500;
    font-size: 20px;
    color: $gray-300;

    transition: color $transition-default;
    cursor: pointer;
    p {
      font-size: 16px;
      @include screen(laptop) {
        font-size: 20px;
      }
    }
    span {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 27px;
      min-width: 27px;
      height: 27px;
      min-height: 27px;

      border-radius: 50%;
      background-color: $blue-400;

      font-weight: 700;
      font-size: 14px;
      line-height: 80%;
    }
    &.active {
      color: $white;
    }
    &:hover {
      color: $white;
    }
  }
  &__button {
    padding: 9px 14px;
    width: 100%;

    border-radius: 100px;

    font-weight: 500;
    font-size: 17px;
    line-height: 100%;
    letter-spacing: -0.33px;
    text-align: center;
    color: $gray-300;

    transition: background-color $transition-default, color $transition-default;
    &.active {
      background-color: $blue-500;

      color: $white;
    }
    @include screen(tablet) {
      width: auto;
    }
    @include screen(laptop) {
      padding: 18px 32px;

      font-size: 22px;
    }
  }
  &__aside {
    padding-top: 20px;
    p {
      font-size: 17px;
      line-height: 140%;
      color: $white;
    }
    a {
      display: inline-flex;
      align-items: center;
      gap: 4px;
      margin-top: 20px;
      padding: 10px 24px;

      border-radius: 22px;
      background-color: $blue-400;

      font-weight: 700;
      color: $white;
      @include screen(laptop) {
        transition: background-color $transition-default;
        &:hover {
          background-color: $blue-500;
        }
      }
    }
  }
  &__inner {
    display: grid;
    grid-template-columns: 1fr;
    gap: 16px;
    @include screen(laptop) {
      grid-template-columns: 223px 1fr;
      gap: 52px;
    }
  }
  &__more {
    padding: 12px;
    width: 100%;

    border-radius: 100px;
    background-color: $blue-500;

    font-weight: 500;
    font-size: 16px;
    line-height: 100%;
    letter-spacing: -0.33px;
    text-align: center;
    color: $white;
    @include screen(laptop) {
      padding: 18px 32px;

      font-size: 22px;

      transition: background-color $transition-default,
        color $transition-default;
      &:hover {
        background-color: $blue-400;
      }
    }
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    gap: 20px;
    padding: 14px 0;
    @include screen(tablet) {
      grid-template-columns: repeat(3, minmax(0, 1fr));
      padding: 20px 0;
    }
    @include screen(laptop) {
      grid-template-columns: repeat(6, minmax(0, 1fr));
    }
    &-aside {
      grid-template-columns: repeat(2, minmax(0, 1fr));
      .vacancies__more {
        grid-column: span 2;
        @include screen(tablet) {
          grid-column: span 3;
          padding: 20px 0;
        }
        @include screen(laptop) {
          grid-column: span 5;
        }
      }
      @include screen(tablet) {
        grid-template-columns: repeat(3, minmax(0, 1fr));
        padding: 20px 0;
      }
      @include screen(laptop) {
        grid-template-columns: repeat(5, minmax(0, 1fr));
      }
    }
  }
  &__item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    padding: 16px 14px;

    border-radius: 16px;
    background-color: #eaecf3;
    h3 {
      display: -webkit-box;
      min-height: 97px;
      max-height: 97px;
      overflow: hidden;

      font-weight: 500;
      font-size: 18px;
      line-height: 22px;
      -webkit-line-clamp: 4;
      -webkit-box-orient: vertical;
    }
    @include screen(tablet) {
      padding: 20px;
    }
  }
  &__prices {
    display: flex;
    flex-direction: column;
    margin-bottom: 11px;
    span {
      font-weight: 500;
      font-size: 15px;
      line-height: 18px;
      color: $blue-500;
    }
  }
  address {
    margin-bottom: 11px;

    font-weight: 500;
    font-style: normal;
    font-size: 15px;
    line-height: 18px;
    color: $blue-400;
  }
  &__company {
    display: flex;
    align-items: center;
    gap: 5px;
    margin-bottom: 11px;
    span {
      font-weight: 700;
      font-size: 10px;
      text-transform: uppercase;
      color: $blue-200;
    }
  }
  &__body {
    display: none;
    &.active {
      display: block;
    }
  }
  &__popup {
    padding: 5px 16px;

    border-radius: 100px;
    background-color: $blue-500;

    font-weight: 600;
    font-size: 12px;
    line-height: 100%;
    letter-spacing: -0.33px;
    text-align: center;
    color: $white;
    @include screen(laptop) {
      transition: background-color $transition-default,
        color $transition-default;
      &:hover {
        background-color: $blue-400;
      }
    }
  }
  &__container {
    display: none;
    &.active {
      display: block;
    }
  }
}